"use client";

import Sidebar from "../sidebar/page";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLayerGroup, faPlus } from "@fortawesome/free-solid-svg-icons";
import { useState } from "react";
import Image from "next/image";
import { Pencil, Trash2 } from "lucide-react";

export default function Category() {
  const itemsPerPage = 3;
  const initialData = [
    { name: 'Apple MacBook Pro 17"', description: 'High-end laptop', image: '/Images/hi.jpeg' },
    { name: 'Microsoft Surface Pro', description: '2-in-1 device', image: '/Images/hi.jpeg' },
    { name: 'Magic Mouse 2', description: 'Wireless mouse', image: '/Images/hi.jpeg' },
    { name: 'iPhone 13', description: 'Smartphone', image: '/Images/hi.jpeg' },
    { name: 'iPad Pro', description: 'Tablet', image: '/Images/hi.jpeg' },
    { name: 'Apple Watch', description: 'Smartwatch', image: '/Images/hi.jpeg' },
    { name: 'AirPods Pro', description: 'Wireless earbuds', image: '/Images/hi.jpeg' },
    { name: 'Dell XPS 13', description: 'Ultrabook', image: '/Images/hi.jpeg' },
  ];

  const [data, setData] = useState(initialData);
  const [currentPage, setCurrentPage] = useState(1);
  const [showModal, setShowModal] = useState(false);
  const [editIndex, setEditIndex] = useState<number | null>(null);
  const [editForm, setEditForm] = useState({ name: '', description: '', image: '' });
  const [imagePreview, setImagePreview] = useState<string | null>(null);

  const totalItems = data.length;
  const totalPages = Math.ceil(totalItems / itemsPerPage);
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = data.slice(indexOfFirstItem, indexOfLastItem);

  const handlePrev = () => setCurrentPage((prev) => Math.max(prev - 1, 1));
  const handleNext = () => setCurrentPage((prev) => Math.min(prev + 1, totalPages));

  const handleEditClick = (index: number) => {
    const item = data[index];
    setEditIndex(index);
    setEditForm(item);
    setImagePreview(item.image);
    setShowModal(true);
  };

  const handleEditChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setEditForm({ ...editForm, [e.target.name]: e.target.value });
  };

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const objectUrl = URL.createObjectURL(file);
      setImagePreview(objectUrl);
      setEditForm((prev) => ({
        ...prev,
        image: objectUrl,
      }));
    }
  };

  const handleSave = () => {
    if (editIndex !== null) {
      const updated = [...data];
      updated[editIndex] = editForm;
      setData(updated);
    }
    setShowModal(false);
    setImagePreview(null);
  };

  const handleDelete = (index: number) => {
    const updated = data.filter((_, i) => i !== index);
    setData(updated);
  };

  return (
    <div className="relative bg-cover bg-center min-h-screen">
      {/* ... (rest of your component stays the same) ... */}

      {showModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl shadow-lg p-6 w-full max-w-md dark:bg-gray-900">
            <h2 className="text-xl font-semibold mb-4 text-gray-800 dark:text-white">Edit Category</h2>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium mb-1 dark:text-gray-300">Name</label>
                <input
                  name="name"
                  type="text"
                  placeholder="Enter category name"
                  value={editForm.name}
                  onChange={handleEditChange}
                  className="w-full border px-3 py-2 rounded-md dark:bg-gray-800 dark:border-gray-600 dark:text-white"
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-1 dark:text-gray-300">Description</label>
                <textarea
                  name="description"
                  placeholder="Enter category description"
                  value={editForm.description}
                  onChange={handleEditChange}
                  className="w-full border px-3 py-2 rounded-md dark:bg-gray-800 dark:border-gray-600 dark:text-white"
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-1 dark:text-gray-300">Upload Image</label>
                <input
                  type="file"
                  accept="image/*"
                  onChange={handleImageUpload}
                  className="w-full border px-3 py-2 rounded-md dark:bg-gray-800 dark:border-gray-600 dark:text-white"
                />
                {imagePreview && (
                  <div className="mt-2">
                    <Image
                      src={imagePreview}
                      alt="Preview"
                      width={80}
                      height={80}
                      className="rounded object-cover"
                    />
                  </div>
                )}
              </div>
              <div className="flex justify-end gap-3 mt-4">
                <button
                  onClick={() => {
                    setShowModal(false);
                    setImagePreview(null);
                  }}
                  className="px-4 py-2 bg-gray-300 text-gray-800 rounded-md"
                >
                  Cancel
                </button>
                <button
                  onClick={handleSave}
                  className="px-4 py-2 bg-blue-600 text-white rounded-md"
                >
                  Save
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
